.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.items{
  display: flex;
  flex-direction: row;
  /* border: 1px solid rebeccapurple; */
  margin-top: -8%;
  width: 56%;
  margin-left: 23%;
  height: 570px;
  
}
.Nav{
  display: flex;
  flex-direction: row;
  margin-left: 2%;
}
li{
  list-style-type: none;
  display: inline-block;
  width: 20px;
  cursor: pointer;
}
